import winImage from './../../images/win.jpg';
import drawImage from './../../images/draw.jpg';

export function createSelectModeModalMarkup() {
  return `<div class="hero">
  <span class="hero__title">  </span>
</div>
<div class="select-mode__wrap">
  <button class="select-mode__btn" data-mode="bot"> '</button>
  <button class="select-mode__btn" data-mode="local"> </button>
  <button class="select-mode__btn" data-mode="online"> </button>
</div>`;
}

export function createSelectLevelModalMarkup() {
  return `<button class="back-btn js-select-player-back-btn"></button>
  <div class="hero">
  <span class="hero__title">  </span>
</div>
<div class="select-level__wrap">
  <button class="select-level__btn" data-level="easy"></button>
  <button class="select-level__btn" data-level="medium"></button>
  <button class="select-level__btn" data-level="hard"></button>
</div>`;
}

export function createSelectPlayersModalMarkup() {
  return `<button class="back-btn js-select-player-back-btn"></button>
  <div class="hero">
      <span class="hero__title"> </span>
    </div>
    <button class="select-player__add" data-player="add">  </button>
    <button class="select-player__delete" data-player="delete"> </button>
    <form class="" name="select-players" autocomplete="off">
      <div class="select-player__wrap">
        <div class="select-player__thumb">
          <label for="player1" class="select-player__label"> 1</label>
          <select class="select-player__select js-first-select" id="player1" name="player1">
          </select>
          <p class="select-player__player1-message form__message"> !</p>
        </div>
        <div class="select-player__thumb">
          <label for="player2" class="select-player__label"> 2</label>
          <select class="select-player__select js-second-select" id="player2" name="player2">
          </select>
          <p class="select-player__player2-message form__message"> !</p>
        </div>
      </div>
      <button class="select-player__start js-start-game"> </button>
    </form>`;
}

export function createAddPlayerModalMarkup() {
  return `<button class="close-btn js-close-modal"></button>
    <form class="" name="newuser" autocomplete="off" id="add-player">
      <p class="player-modal__title">  </p>
      <div class="player-modal__wrap">
      <input
        class="player-modal__input"
        type="text"
        name="playername"
        placeholder=" ' "
        id='newuserinput'
      />
      <p class="player-modal__message"></p>
</div>
      <div class="player-modal__btn-wrap">
        <button class="add-player__btn js-add-player" type="submit"></button>
        <button class="add-player__btn js-close-modal"></button>
      </div>
    </form>`;
}

export function createDeletePlayerModalMarkup() {
  return `<button class="close-btn js-close-modal"></button>
    <form class="" name="newuser" autocomplete="off" id="delete-player">
      <p class="player-modal__title">   </p>
      <div class="player-modal__wrap">
      <select class="delete-player__select" id="deleteuser" name="deleteuser">
</select>
<p class="js-delete-message form__message"> !</p>
</div>
<div class="player-modal__btn-wrap">
<button class="delete-player__btn js-delete-player" type="submit"></button>
<button class="delete-player__btn js-close-modal"></button>
</div>
</form>`;
}

export function createVictoryModalMarkup(playerName) {
  return `<img class="win__img" src="${winImage}" alt="" />
    <p class="win__message">${playerName} ()!</p>
    <div class="win__wrap">
      <button class="win__btn" data-victory="mode"> </button>
      <button class="win__btn" data-victory="player"> </button>
      <button class="win__btn" data-victory="game"> </button>
    </div>`;
}

export function createDrawModalMarkup() {
  return `<img class="draw__img" src="${drawImage}" alt="" />
    <p class="draw__result"></p>
    <p class="draw__message"> !</p>
    <div class="draw__wrap">
      <button class="draw__btn" data-draw="mode"> </button>
      <button class="draw__btn" data-draw="player"> </button>
      <button class="draw__btn" data-draw="game"> </button>
    </div>`;
}

